import './App.css'
import { useState } from 'react'
import teoria from 'teoria'
import teoriaChordProgression from 'teoria-chord-progression'
import * as Tone from 'tone'

const Mousetrap = require('mousetrap')

const synth = new Tone.PolySynth(Tone.AMSynth).toDestination()

let releaseTimeOut = null

function App () {

  const chordLengths = [3, 4]
  const [chordLength, setChordLength] = useState(chordLengths[0])

  const romanNumerals = ['I', 'II', 'III', 'IV', 'V', 'VI', 'VII']

  const keys = ['C', 'D', 'E', 'F', 'G', 'A', 'B']
  const [key, setKey] = useState(keys[0])

  const modes = ['ionian', 'dorian', 'phrygian', 'lydian', 'mixolydian', 'aeolian', 'locrian']
  const [mode, setMode] = useState(modes[0])

  const scale = teoria.scale(key, mode)
  const positions = Array.from(Array(scale.scale.length + 1).keys()).slice(1)
  const chordProgression = teoriaChordProgression(scale, positions, chordLength)

  const [currentChord, setCurrentChord] = useState(null)

  const play = (chord) => {
    clearTimeout(releaseTimeOut)
    synth.releaseAll()
    setCurrentChord(chord.name)

    chord.notes().forEach(note => {
      synth.triggerAttack(note.toString())
    })

    releaseTimeOut = setTimeout(() => {
      synth.releaseAll()
      setCurrentChord(null)
    }, 2500)
  }

  Mousetrap.reset()

  return (
    <div className="App">
      <select onChange={e => {setKey(e.target.value)}} value={key}>
        {keys.map(note => <option value={note} key={note}>{note}</option>)}
      </select>
      <select onChange={e => {setMode(e.target.value)}} value={mode}>
        {modes.map((scaleName, index) => <option value={scaleName} key={scaleName}>{romanNumerals[index]}</option>)}
      </select>
      <select onChange={e => {setChordLength(parseInt(e.target.value))}} value={chordLength}>
        {chordLengths.map(length => <option value={length} key={length}>{length} notes per chord</option>)}
      </select>
      <p>
        {key} {mode}
      </p>

      {chordProgression.getChords().map((chord, index) => {
        Mousetrap.bind((index + 1).toString(), () => play(chord))
        return <button style={{ padding: 10, margin: 10 }} key={chord.name}
                       onClick={() => play(chord)}>{romanNumerals[index]}</button>
      })}
      <p>
        {currentChord}
      </p>
    </div>
  )
}

export default App
